import React, { useRef, useState, useEffect } from "react";
import { useFrame } from "@react-three/fiber";
import { Float, Text, Sphere } from "@react-three/drei";
import * as THREE from "three";

// Technologies to display in orbits
const TECH_NAMES = [
  // Inner orbit - Core technologies
  ["React", "Node", "JS", "Python", "SQL"],
  // Middle orbit - Frameworks & tools
  ["AWS", "Docker", "Git", "MongoDB", "Express"],
  // Outer orbit - Concepts & paradigms
  ["Cloud", "API", "TDD", "Web", "Security"]
];

// Different colors for each orbit
const ORBIT_COLORS = [
  "#40e0d0", // Teal
  "#7b68ee", // Medium slate blue
  "#8f5cff"  // Purple
];

// Orbital rings
const OrbitalRings = () => {
  const ringsRef = useRef();
  
  useFrame((state) => {
    if (ringsRef.current) {
      const t = state.clock.getElapsedTime() * 0.1;
      ringsRef.current.rotation.x = Math.sin(t) * 0.3;
      ringsRef.current.rotation.y = Math.cos(t) * 0.2;
    }
  });
  
  return (
    <group ref={ringsRef}>
      {[1.5, 2.5, 3.5].map((radius, idx) => (
        <mesh key={idx} rotation={[Math.PI / (idx + 2), 0, 0]}>
          <torusGeometry args={[radius, 0.02, 16, 100]} />
          <meshBasicMaterial 
            color={ORBIT_COLORS[idx]} 
            transparent 
            opacity={0.3} 
          />
        </mesh>
      ))}
    </group>
  );
};

// Core sphere in the center
const Core = () => {
  const ref = useRef();

  useFrame((state) => {
    const t = state.clock.getElapsedTime();
    ref.current.scale.x = Math.sin(t / 2) * 0.05 + 1;
    ref.current.scale.y = Math.sin(t / 2) * 0.05 + 1;
    ref.current.scale.z = Math.sin(t / 2) * 0.05 + 1;
  });

  return (
    <Sphere ref={ref} args={[0.7, 32, 32]} position={[0, 0, 0]}>
      <meshPhongMaterial 
        color="#40e0d0" 
        emissive="#40e0d0"
        emissiveIntensity={0.5}
        metalness={0.8}
        roughness={0.2}
      />
    </Sphere>
  );
};

// Orbit of tech items
const TechOrbit = ({ radius, items, color, speed, textRef }) => {
  const orbitRef = useRef();
  const itemRefs = useRef([]);
  
  useEffect(() => {
    itemRefs.current = Array(items.length).fill().map(() => React.createRef());
  }, [items.length]);
  
  useFrame((state) => {
    // Rotate the orbit
    if (orbitRef.current) {
      orbitRef.current.rotation.y += 0.002 * speed;
    }
    
    // Make text always face camera
    itemRefs.current.forEach(ref => {
      if (ref.current) {
        ref.current.lookAt(state.camera.position);
      }
    });
  });
  
  return (
    <group ref={orbitRef}>
      {items.map((item, i) => {
        const angle = (i / items.length) * Math.PI * 2;
        const x = Math.cos(angle) * radius;
        const z = Math.sin(angle) * radius;
        
        return (
          <group key={i} position={[x, 0, z]}>
            <Sphere args={[0.2, 16, 16]}>
              <meshStandardMaterial color={color} />
            </Sphere>
            <Text
              ref={el => itemRefs.current[i] = el}
              position={[0, 0.35, 0]}
              fontSize={0.2}
              color={color}
              anchorX="center"
              anchorY="middle"
              outlineWidth={0.01}
              outlineColor="#111827"
            >
              {item}
            </Text>
          </group>
        );
      })}
    </group>
  );
};

const TechOrbit3D = ({ interactive = true }) => {
  const groupRef = useRef();
  const [hover, setHover] = useState(false);
  const [autoRotate, setAutoRotate] = useState(true);
  const textRef = useRef();
  
  // Handle hover and click effects
  const handlePointerOver = () => setHover(true);
  const handlePointerOut = () => setHover(false);
  const handleClick = () => setAutoRotate(!autoRotate);
  
  // Animation
  useFrame((state, delta) => {
    if (!groupRef.current) return;
    
    // Auto rotation if enabled
    if (autoRotate) {
      groupRef.current.rotation.y += delta * 0.1;
    }
    
    // Hover effect - slight tilt upward when hovered
    if (hover && interactive) {
      groupRef.current.rotation.x = THREE.MathUtils.lerp(
        groupRef.current.rotation.x,
        -0.3,
        0.1
      );
    } else {
      groupRef.current.rotation.x = THREE.MathUtils.lerp(
        groupRef.current.rotation.x,
        0,
        0.1
      );
    }
  });
  
  return (
    <Float
      speed={1.5}
      rotationIntensity={0.2}
      floatIntensity={0.3}
      floatingRange={[-0.05, 0.05]}
    >
      <group 
        ref={groupRef}
        onPointerOver={interactive ? handlePointerOver : undefined}
        onPointerOut={interactive ? handlePointerOut : undefined}
        onClick={interactive ? handleClick : undefined}
        position={[0, 0, 0]}
        scale={[0.8, 0.8, 0.8]}
      >
        <Core />
        <OrbitalRings />
        <TechOrbit radius={1.5} items={TECH_NAMES[0]} color={ORBIT_COLORS[0]} speed={1.5} textRef={textRef} />
        <TechOrbit radius={2.5} items={TECH_NAMES[1]} color={ORBIT_COLORS[1]} speed={1.0} textRef={textRef} />
        <TechOrbit radius={3.5} items={TECH_NAMES[2]} color={ORBIT_COLORS[2]} speed={0.7} textRef={textRef} />
      </group>
    </Float>
  );
};

export default TechOrbit3D;